.layer-1-icon {
  margin-top: -1px;
  margin-bottom: -1px;
  height: 33.1px;
  width: 91.8px;
  position: relative;
}
.layer-1-icon1 {
  height: 27.4px;
  width: 83.2px;
  position: relative;
}
.asset-2-2,
.footer-container1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.asset-2-2 {
  overflow: hidden;
  padding: var(--padding-12xs) 0 0;
  gap: 0 9px;
}
.footer-container1 {
  width: 100%;
  flex-wrap: wrap;
  padding: var(--padding-3xs) var(--padding-11xl);
  box-sizing: border-box;
  max-width: 1400px;
}
.divider {
  align-self: stretch;
  height: 3px;
  position: relative;
  background-color: var(--color-gray);
  overflow: hidden;
  flex-shrink: 0;
}
.kitsumondao-llc1 {
  text-decoration: underline;
}
.kitsumondao-llc {
  color: inherit;
}
.project-name {
  flex: 1;
  display: inline-block;
  min-width: 90px;
  max-width: 100%;
}
.legal,
.privacy,
.project-name {
  position: relative;
  line-height: 160%;
}
.bottom-footer-info,
.privacy-legal {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
}
.privacy-legal {
  width: 165px;
  gap: 16px 5.16px;
  margin-left: -6px;
}
.bottom-footer-info {
  align-self: stretch;
  padding: 0 var(--padding-7xs) 0 0;
  box-sizing: border-box;
  max-width: 100%;
}
.bottom-footer-container,
.footer-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-11xl);
  box-sizing: border-box;
  gap: 16px 0;
  max-width: 1400px;
}
.footer-container {
  width: 1440px;
  align-items: flex-start;
  padding: 0 var(--padding-xl);
  gap: 11px 0;
  max-width: 100%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-white);
  font-family: var(--font-kanit);
}
@media screen and (max-width: 1400px) {
  .bottom-footer-container,
  .footer-container1 {
    max-width: 100%;
  }
}
